
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { EventPair } from './event-list/event-list.component';
import { BaseDirective } from 'src/app/shared/base.directive';
import { ApplicationService } from 'src/app/services/app.service';

@Component({
    selector: 'app-schedule',
    templateUrl: './schedule.template.html',
    styleUrls: ['./schedule.styles.scss'],
})
export class ScheduleComponent extends BaseDirective implements OnInit {
    /** Name of the page to render */
    public page: string;
    /** ID of the event item to view */
    public id: string;
    /** Current list of loaded events */
    public events: EventPair[];
    /** Whether to show menu */
    public show_menu = false;

    /** ID of the event after the active event */
    public get next(): string {
        if (this.events) {
            const index = this.events.findIndex(i => this.id === i.id) + 1;
            if (index > -1 && this.events[index]) {
                return this.events[index].id;
            }
        }
        return '';
    }

    /** ID of the event before the active event */
    public get previous(): string {
        if (this.events) {
            const index = this.events.findIndex(i => this.id === i.id) - 1;
            if (index > -1 && this.events[index]) {
                return this.events[index].id;
            }
        }
        return '';
    }

    constructor(private _route: ActivatedRoute, private _service: ApplicationService) {
        super();
    }

    public ngOnInit(): void {
        this.subscription('route.route', this._route.paramMap.subscribe((params) => {
            if (params.has('page')) {
                this.page = params.get('page');
            }
            if (params.has('id')) {
                this.id = params.get('id');
            }
        }));
        this.loadEvents();
        this._service.title = 'My Day';
    }

    /**
     * Update local event listing
     * @param event_list New list of events
     */
    public updateEvents(event_list: EventPair[]) {
        this.events = event_list;
        this.saveEvents();
    }

    /**
     * Save the current event listing
     */
    public saveEvents() {
        if (localStorage) {
            localStorage.setItem('STAFF.events', JSON.stringify(this.events || []));
        }
    }

    /**
     * Load the last saved event listing
     */
    public loadEvents() {
        if (localStorage) {
            const data = localStorage.getItem('STAFF.events');
            this.events = JSON.parse(data || '[]');
        }
    }
}
