import { Component, OnInit, EventEmitter, Input, Output, SimpleChanges, OnChanges, OnDestroy } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';

import { BaseDirective } from 'src/app/shared/base.directive';
import { Booking } from 'src/app/services/data/bookings/booking.class';
import { ApplicationService } from 'src/app/services/app.service';
import { generateBookingForm } from 'src/app/services/data/bookings/booking.utilities';

import * as dayjs from 'dayjs';
import { BookingConfirmComponent } from 'src/app/shell/bookings/overlays/booking-confirm/booking-confirm.component';

@Component({
    selector: 'schedule-event-edit',
    templateUrl: './event-edit.component.html',
    styleUrls: ['./event-edit.component.scss']
})
export class EventEditComponent extends BaseDirective implements OnInit, OnChanges, OnDestroy {
    /** Booking to display */
    @Input() public event: Booking;
    /** Emitter for action on the display view */
    @Output() public change = new EventEmitter<{ type: string }>();
    /** Emitter for changes to the loading state */
    @Output() public loading = new EventEmitter<string>();
    /** Form field data */
    public form: FormGroup;

    constructor(private _service: ApplicationService, private _dialog: MatDialog) {
        super();
    }

    public ngOnInit(): void {
        /* istanbul ignore else */
        if (this.event) {
            this.form = generateBookingForm(
                this.event,
                this._service.Users.current,
                this._service.setting('app.booking.show_fields') || []
            );
        }
    }

    public ngOnChanges(changes: SimpleChanges): void {
        /* istanbul ignore else */
        if (changes.event && this.event) {
            /* istanbul ignore else */
            if (this.form && changes.event.previousValue) {
                (changes.event.previousValue as Booking).emit('item_saved');
            }
            this.form = generateBookingForm(
                this.event,
                this._service.Users.current,
                this._service.setting('app.booking.show_fields') || []
            );
        }
    }

    public ngOnDestroy() {
        super.ngOnDestroy();
        /* istanbul ignore else */
        if (this.form && this.event) {
            this.event.emit('item_saved');
        }
    }

    /**
     * Exit the edit form
     */
    public cancel() {
        this.change.emit({ type: 'cancel' });
    }

    /**
     * Save changes to the booking
     */
    public save() {
        /* istanbul ignore else */
        if (!this.event.has_changes) {
            this._service.notifyInfo('No changes have been made to booking');
            return;
        }
        /* istanbul ignore else */
        if (this.form.valid) {
            const dialog_ref = this._dialog.open(BookingConfirmComponent, {
                width: '32em',
                maxWidth: '95vw',
                maxHeight: '95vh',
                data: { booking: this.event }
            });
            dialog_ref.componentInstance.event.subscribe(event => {
                /* istanbul ignore else */
                if (event.reason === 'success') {
                    this.change.emit({ type: 'view' });
                }
            });
        }
    }
}
