import { Component, Input, Output, EventEmitter } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';

import { BaseDirective } from 'src/app/shared/base.directive';
import { Booking } from 'src/app/services/data/bookings/booking.class';
import { humaniseDuration } from 'src/app/shared/utilities/general.utilities';
import { Space } from 'src/app/services/data/spaces/space.class';
import { User } from 'src/app/services/data/users/user.class';
import { ApplicationService } from 'src/app/services/app.service';
import { ViewRoomModalComponent } from 'src/app/overlays/view-room-modal/view-room-modal.component';

import * as dayjs from 'dayjs';

@Component({
    selector: 'schedule-event-details',
    templateUrl: './event-details.component.html',
    styleUrls: ['./event-details.component.scss']
})
export class EventDetailsComponent extends BaseDirective {
    /** Booking to display */
    @Input() event: Booking;
    /** Emitter for action on the display view */
    @Output() change = new EventEmitter();
    /** Emitter for changes to the loading state */
    @Output() loading = new EventEmitter();
    /** Toggle for activating checkin */
    public checked_in: boolean;
    /** Mapping of spaces to last checkin time */
    public last_start: { [id: string]: number } = {};
    /** Whether to show full locations list */
    public show_locations: boolean;
    /** Whether to show full attendee list */
    public show_attendees: boolean;

    /** Title of the displayed event */
    public get title(): string {
        if (!this.event) {
            return '';
        }
        return this.event.title;
    }

    /** Display string of when the event will occur */
    public get when(): string {
        if (!this.event) {
            return 'Unable to determine.';
        }
        const date = dayjs(this.event.date);
        const end = date.add(this.event.duration || 60, 'm');
        if (this.event.all_day || this.event.duration > 23 * 60) {
            return `${date.format('DD MMM YYYY')} - All Day`;
        } else {
            if (date.isSame(end, 'd')) {
                return `${date.format('DD MMM YYYY, h:mm A')} - ${end.format('h:mm A')}`;
            } else {
                return `${date.format('DD MMM YYYY, h:mm A')} - ${end.format(
                    'DD MMM YYYY, h:mm A'
                )}`;
            }
        }
    }

    /** Display string for the event's duration */
    public get duration(): string {
        if (!this.event) {
            return 'Unable to determine';
        }
        return humaniseDuration(this.event.duration || 60);
    }

    /** List of spaces associated with the event */
    public get spaces(): Space[] {
        if (!this.event) {
            return [];
        }
        return this.event.space_list;
    }

    /** List of attendees associated with the event not including the host */
    public get attendees(): User[] {
        if (!this.event) {
            return [];
        }
        return this.event.attendees.filter(i => i.email !== this.organiser.email);
    }

    /** Host of the event */
    public get organiser(): User {
        if (!this.event) {
            return this._service.Users.current;
        }
        return this.event.organiser || this._service.Users.current;
    }

    /** Whether the user is allowed to edit the event */
    public get can_edit(): boolean {
        const organiser = this.organiser;
        const user = this._service.Users.current;
        return organiser.email === user.email && this.status !== 'done';
    }

    /** Details about the event */
    public get notes(): string {
        if (!this.event) {
            return '';
        }
        return this.event.description || '';
    }

    /** Status of the event */
    public get status(): 'future' | 'upcoming' | 'done' | 'started' | 'in_progress' {
        if (this.event) {
            const now = dayjs();
            const date = dayjs(this.event.date);
            /* istanbul ignore else */
            if (now.isBefore(date.subtract(15, 'm'), 'm')) {
                return 'future';
            } else if (now.isBefore(date, 'm')) {
                return 'upcoming';
            } else if (now.isBefore(date.add(15, 'm'), 'm')) {
                return 'started';
            } else if (now.isBefore(date.add(this.event.duration, 'm'), 'm')) {
                return 'in_progress';
            }
        }
        return 'done';
    }

    /** Whether the user has checkin to all the spaces in the event */
    public get is_checked_in(): boolean {
        return this.spaces.reduce(
            (a, v) => a && this.event.date <= (this.last_start[v.id] || 0),
            true
        );
    }

    /** Whether the spaces can be controlled */
    public get can_control(): boolean {
        return this.spaces.length > 0 && this.status !== 'future' && this.status !== 'done';
    }

    /** Whether the user is able to checkin to the event */
    public get can_checkin(): boolean {
        return this.spaces.length > 0 && (this.status === 'upcoming' || this.status === 'started');
    }

    constructor(private _service: ApplicationService, private _dialog: MatDialog, private _router: Router) {
        super();
    }

    /**
     * Open the modal to show the location of a space
     * @param space Space to show the location
     */
    public locate(space: Space) {
        /* istanbul ignore else */
        if (space) {
            this._dialog.open(ViewRoomModalComponent, {
                width: 'auto',
                height: 'auto',
                maxWidth: '95vw',
                maxHeight: '95vh',
                data: { space }
            });
        }
    }

    /**
     * Enter edit mode for the booking
     */
    public edit(): void {
        this.change.emit({ type: 'edit' });
    }

    /**
     * Checkin the current booking
     */
    public checkin(): void {
        this.checked_in = !this.checked_in;
    }

    /**
     * Store the booking data and navigate to the booking form
     */
    public duplicate(): void {
        /* istanbul ignore else */
        if (localStorage) {
            const booking = this.event.duplicate();
            localStorage.setItem('STAFF.booking_form', JSON.stringify(booking));
        }
        this.change.emit({ type: 'duplicate' });
        this._router.navigate(['/book']);
    }
}
