import { Component, OnInit, Input, SimpleChanges, OnChanges } from '@angular/core';
import { Router } from '@angular/router';

import { BaseDirective } from 'src/app/shared/base.directive';
import { Booking } from 'src/app/services/data/bookings/booking.class';
import { ApplicationService } from 'src/app/services/app.service';

@Component({
    selector: 'schedule-view-event',
    templateUrl: './view-event.component.html',
    styleUrls: ['./view-event.component.scss']
})
export class ScheduleViewEventComponent extends BaseDirective implements OnChanges, OnInit {
    /** ID of the current booking to display */
    @Input() public id: string;
    /** ID of the next booking */
    @Input() public next: string;
    /** ID of the previous booking */
    @Input() public previous: string;
    /** Booking to display */
    public event: Booking;
    /** Whether booking data is being loaded */
    public loading: string;
    /** Whether booking is being edited */
    public editing: boolean;

    constructor(private _service: ApplicationService, private _router: Router) {
        super();
    }

    public ngOnInit(): void {
        this.subscription(
            'arrow_right',
            this._service.Hotkeys.listen(['ArrowRight'], () => this.nextBooking())
        );
        this.subscription(
            'arrow_left',
            this._service.Hotkeys.listen(['ArrowLeft'], () => this.previousBooking())
        );
        this.subscription(
            'changes',
            this._service.Bookings.listen('list').subscribe(() => this.loadEvent())
        );
    }

    public ngOnChanges(changes: SimpleChanges): void {
        /* istanbul ignore else */
        if (changes.id && this.id) {
            this.event = null;
            this.editing = false;
            this.loading = '';
            this.loadEvent();
        }
    }

    /**
     * Load the details of the active event
     */
    public loadEvent() {
        this.loading = 'Loading booking data...';
        let booking = this._service.Bookings.find(this.id);
        if (booking) {
            this.event = booking;
            this.loading = '';
        } else {
            this._service.Bookings.show(this.id, {}).then(
                bkn => {
                    this.loading = '';
                    this.event = bkn;
                },
                () => {
                    this.loading = '';
                    this._service.notifyError(`Failed to loaded booking data<br>ID: ${this.id}`);
                    this._router.navigate(['/schedule']);
                }
            );
        }
    }

    /**
     * View the next booking in the list
     */
    public nextBooking() {
        /* istanbul ignore else */
        if (this.next && !this.editing) {
            this._router.navigate(['/schedule', 'view', this.next]);
        }
    }

    /**
     * View the previous booking in the list
     */
    public previousBooking() {
        /* istanbul ignore else */
        if (this.previous && !this.editing) {
            this._router.navigate(['/schedule', 'view', this.previous]);
        }
    }

    /**
     * Handle changes to sub-components
     */
    public onChange(event: { type: string }): void {
        /* istanbul ignore else */
        if (event.type === 'edit') {
            this.editing = true;
        } else if (event.type === 'view' || event.type === 'cancel') {
            this.editing = false;
        }
    }
}
