import { Component, ViewChild, Output, EventEmitter } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CdkVirtualScrollViewport } from '@angular/cdk/scrolling';

import { ApplicationService } from '../../../services/app.service';
import { Booking } from 'src/app/services/data/bookings/booking.class';
import { unique } from 'src/app/shared/utilities/general.utilities';
import { BaseDirective } from 'src/app/shared/base.directive';
import { BookingLike } from './list-item/list-item.component';

import * as dayjs from 'dayjs';
import { User } from 'src/app/services/data/users/user.class';
import { MatMenuTrigger } from '@angular/material/menu';

export interface EventPair {
    id: string;
    date: number;
}

@Component({
    selector: 'schedule-event-list',
    templateUrl: './event-list.component.html',
    styleUrls: ['./event-list.component.scss']
})
export class ScheduleEventListComponent extends BaseDirective {
    /** Emitter  */
    @Output('eventList') public event_list = new EventEmitter<EventPair[]>();
    /** List of items to display on the event list */
    public events: BookingLike[] = [];
    /** Update promise */
    public event_promise: Promise<void>;
    /** Whether bookings are being updated */
    public loading: boolean;
    /** Number of days to render in the list from the current date */
    public shown_offset: number = 7;
    /** Offset of the date to show events for */
    public date_offset: number = 0;
    /** Whether to show the calendar tooltip */
    public show_calendar: boolean;
    /** Date to scroll to on next update */
    public scroll_to: string;
    /** Current UTC epoch */
    public now: number = dayjs().valueOf();
    /** Current UTC epoch */
    public date: number;
    /** Whether list is displaying the top of the first item */
    public at_top: boolean = true;
    /** List of users available to view schedules for */
    public user_list: User[] = [];
    /** Active user to display schedule for */
    public active_user: User;

    /** Viewport for the scrollable area of the list */
    @ViewChild(CdkVirtualScrollViewport, { static: true })
    private scroll_viewport: CdkVirtualScrollViewport;

    @ViewChild(MatMenuTrigger, { static: true }) trigger: MatMenuTrigger;

    constructor(private _service: ApplicationService, private _route: ActivatedRoute) {
        super();
    }

    public ngOnInit(): void {
        this.subscription(
            'route.query',
            this._route.queryParamMap.subscribe(params => {
                /* istanbul ignore else */
                if (params.has('email')) {
                    this.active_user = this._service.Users.find(params.get('email')) || new User(this._service.Users, { email: params.get('email') });
                }
            })
        );
        this.updateEvents();
        this.interval('update_events', () => this.updateEvents(), 10 * 1000);
        this.subscription(
            'scroll',
            this.scroll_viewport.elementScrolled().subscribe(() => this.onScroll())
        );
        this.subscription('delegate_list', this._service.Users.listen('delegates').subscribe((list) => {
            /* istanbul ignore else */
            if (list && list[0]) {
                this.user_list = [this._service.Users.current].concat(list);
                /* istanbul ignore else */
                if (!this.active_user) {
                    this.active_user = this.user_list[0];
                }
            }
        }));
    }

    /**
     * Update the list of events for the currently visible period
     */
    public updateEvents() {
        /* istanbul ignore else */
        if (!this.event_promise) {
            this.event_promise = new Promise(resolve => {
                const start = dayjs()
                    .add(this.date_offset, 'd')
                    .startOf('d');
                const end = start.add(7, 'd').endOf('d');
                this.loading = true;
                this._service.Bookings.query({
                    email: (this.active_user ? this.active_user.email : '') || this._service.Users.current.email,
                    from: start.unix(),
                    to: end.unix()
                }).then(
                    (list: Booking[]) => {
                        this._service.Bookings.updateList(this._service.Bookings.filter(), list);
                        const old_events = this.events.filter(i => {
                            const date = dayjs(i.date);
                            return (
                                i.type !== 'date' &&
                                i.type !== 'empty' &&
                                (date.isBefore(start, 's') || date.isAfter(end, 's'))
                            );
                        });
                        this.events = this.processEvents(list, old_events);
                        // Update event list for view listing
                        const event_list = this.events.filter(
                            i => i.type !== 'date' && i.type !== 'empty'
                        );
                        this.event_list.emit(event_list.map(i => ({ id: i.id, date: i.date })));
                        // Scroll to date if needed
                        this.timeout('scroll_to', () => this.scrollTo());
                        this.loading = false;
                        delete this.event_promise;
                    },
                    () => {
                        this.loading = false;
                        delete this.event_promise;
                    }
                );
            });
        }
        return this.event_promise;
    }

    /**
     * Process list of new items and merge with the old
     * @param list List of new bookings to generate event blocks for
     * @param old_events List of relevant old event blocks
     */
    public processEvents(list: Booking[], old_events: BookingLike[]): BookingLike[] {
        let events: BookingLike[] = [];
        const now = dayjs();
        let date = dayjs().startOf('d');
        const end = date.add(this.shown_offset, 'd').endOf('d');
        for (; date.isBefore(end); date = date.add(1, 'd')) {
            const date_str = date.format('YYYY-MM-DD');
            // Add date block
            events.push({
                id: date_str,
                type: 'date',
                order_id: date_str,
                title: date.format('dddd, DD MMM'),
                is_today: date.isSame(now, 'd'),
                date: date.valueOf()
            });
            const filter_fn: (_: BookingLike) => boolean = i => {
                const day = dayjs(i.date);
                return day.isAfter(date, 's') && day.isBefore(date.endOf('d'), 's');
            };
            // Add events for today to list
            const new_items = list.filter(filter_fn as any);
            const old_items = old_events.filter(filter_fn as any);
            if (new_items.length || old_items.length) {
                events = events.concat(new_items as any).concat(old_items);
            } else {
                // Add block for no events if so
                events.push({
                    id: `${date_str}-empty`,
                    type: 'empty',
                    order_id: date_str,
                    title: `No items for ${date.format('dddd, DD MMM')}`,
                    is_today: date.isSame(now, 'd'),
                    date: date.valueOf() + 1
                });
            }
        }
        events.sort((a, b) => a.date - b.date);
        events = unique(events, 'id');
        return events;
    }

    /**
     * Handle scrolling on the event list
     */
    public onScroll() {
        /* istanbul ignore else */
        if (!this.event_promise) {
            this.show_calendar = false;
            const range = this.scroll_viewport.getRenderedRange();
            const dist = range.end - range.start;
            const nearest = this.events[range.start];
            this.at_top = range.start === 0 && this.scroll_viewport.measureScrollOffset() < 5;
            this.date_offset = dayjs(nearest.date).diff(dayjs(), 'd') - 1;
            this.date = dayjs((this.events[dist + range.start] || {}).date)
                .subtract(1, 'd')
                .valueOf();
            /* istanbul ignore else */
            if (range.end > this.events.length - 7) {
                this.shown_offset += 3;
                this.updateEvents();
            }
        }
    }

    /**
     * Update the date offset
     * @param date UTC milliseconds epoch of new date offset
     */
    public updateDate(date: number) {
        const day = dayjs(date);
        this.date_offset = day.diff(dayjs(), 'd') - 1;
        this.shown_offset = Math.max(this.shown_offset, this.date_offset + 3);
        this.scroll_to = day.format('YYYY-MM-DD');
        delete this.event_promise;
        this.show_calendar = false;
        this.date = dayjs()
            .add(this.date_offset, 'd')
            .valueOf();
        this.updateEvents();
    }

    /** Close the menu popup with the date picker */
    public closeDatePicker() {
        this.trigger.closeMenu();
    }

    /* istanbul ignore next */
    public trackByFn(item: BookingLike, index: number) {
        return item ? item.id : null;
    }

    /**
     * Scroll to a previously defined date on the event list
     */
    private scrollTo() {
        /* istanbul ignore else */
        if (this.scroll_to) {
            const index = this.events.findIndex(i => i.id === this.scroll_to);
            /* istanbul ignore else */
            if (index > -1) {
                this.scroll_viewport.scrollToIndex(index, 'smooth');
                this.scroll_to = null;
            }
        }
    }
}
